.section {
    padding: 20px;
    box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
                0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}

.title {
    font-size: 36px;
    font-weight: 600;
    margin-bottom: 10px;
}

.siteNav {
    font-size: 24px;
    font-weight: 600;
}

.item:not(:last-child) {
    margin-bottom: 10px;
}

.activeSiteNav {
    color: rgb(240, 88, 88);
}